

.workbank_box{
  background-color: #E0BFE6;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-top: 0px;
}
.workbank_content{
  background-color: white;
  box-sizing: border-box;
  width: fit-content;
  height: fit-content;
  border: solid #692498 5px;
  text-align: justify;
  padding: 5px;
  display: grid;
  margin-bottom: 0px;
}
.title_workbank{
  grid-column: 1/7; 
  margin-top: 10px;
  padding-bottom: 0px;
  font-size: 30px;
  justify-content: left;
}
.coretable_workbank{
  display: grid;
  grid-column: 1/7;
  
  margin-top: 20px;
  padding-bottom: 20px;
}

.add_problem_class_box{
    display: inline;
}

.main_problem_div{
  height: fit-content;
}